@mixin flex-container($justify-content, $direction) {
  display: flex;
  align-items: center;
  justify-content: $justify-content;
  flex-direction: $direction; }

.flex {
  @include flex-container(center, row); }

.flexBtn {
  @include flex-container(center, row);
  margin: 0;
  padding: 0; }


.divBtn {
  margin: 0;
  padding: 0 15px 0 15px; }


.btnBlack {
  background-color: black;
  margin: 0;
  padding: 0; }

.btnGray {
  background-color: gray;
  margin-bottom: 2px;
  padding: 0; }


.divInfoRating {
  margin: 0;
  padding: 0px 0px 10px 5px; }

.divInfoRank {
  margin: 0;
